import React, { Component } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CInputFile,
  CLabel,
} from "@coreui/react";

import API from "src/api";
import { CompactPicker } from "react-color";

import SunEditor from "suneditor-react";
import "suneditor/dist/css/suneditor.min.css";

import { history } from "src/history";

const editorConfig = [
  ["align", "fontColor", "fontSize"],
  ["bold", "underline", "italic", "strike", "subscript", "superscript"],
  ["undo", "redo"],
  ["outdent", "indent"],
];

class AddChapter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      preFix: "",
      subTitle: "",
      introduction: "",
      colorCode: "",
      imageFile: "",
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleFileChange = this.handleFileChange.bind(this);
    this.handleColorChange = this.handleColorChange.bind(this);
    this.submitButton = this.submitButton.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  }
  handleFileChange(event) {
    this.setState({ imageFile: event.target.files[0] });
  }
  handleColorChange(color) {
    this.setState({ colorCode: color.hex });
  }

  handleEditorChange(content) {
    this.setState({
      subTitle: content,
    });
  }

  async submitButton() {
    const formData = new FormData();
    formData.append("title", this.state.title);
    formData.append("preFix", this.state.preFix);
    formData.append("subTitle", this.state.subTitle);
    formData.append("introduction", this.state.introduction);
    formData.append("colorCode", this.state.colorCode);
    formData.append("file", this.state.imageFile);

    await API.post("/chapter/add", formData)
      .then((response) => {
        console.log(response.code);
      })
      .catch((error) => {
        alert("Try Again");
      });
    this.resetButton();
  }

  resetButton = () => {
    this.setState(
      (prevState) => ({
        title: "",
        preFix: "",
        subTitle: "",
        introduction: "",
        imageFile: "",
        colorCode: "",
      }),
      () => {}
    );
  };

  render() {
    let { preFix, title, subTitle, introduction, colorCode } = this.state;
    return (
      <>
        <CCol xs="12">
          <CCard>
            <CCardHeader>
              <span className="h4">Add Chapter</span>
              {/* <Link to={{pathname:`/chapter/`}} className="btn btn-outline-info float-right mx-2"> */}
              <CButton
                className="btn btn-outline-info float-right mx-2"
                onClick={() => {
                  history.goBack();
                }}
              >
                Back
              </CButton>
              {/* </Link> */}
            </CCardHeader>
            <CCardBody>
              <CForm
                action=""
                method="post"
                encType="multipart/form-data"
                className="form-horizontal"
              >
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="preFix">Title PreFix</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput
                      onChange={this.handleInputChange}
                      type="text"
                      id="preFix"
                      name="preFix"
                      value={preFix}
                      placeholder="Enter Chapter Prefix"
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="title">Title</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput
                      onChange={this.handleInputChange}
                      id="title"
                      name="title"
                      value={title}
                      placeholder="Title"
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="subTitle">SubTitle</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    {/* <CInput onChange={this.handleInputChange} type="text" id="subTitle" name="subTitle" placeholder="Enter Chapter SubTitle"/>                     */}
                    <SunEditor
                      defaultValue={subTitle}
                      setOptions={{ buttonList: editorConfig }}
                      height="50%"
                      onChange={(content) => {
                        this.handleEditorChange(content);
                      }}
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="intoduction">Introduction</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput
                      onChange={this.handleInputChange}
                      type="text"
                      id="introduction"
                      name="introduction"
                      value={introduction}
                      placeholder="Enter Chapter Intoduction"
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="colorCode">color</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CompactPicker
                      color={colorCode}
                      onChangeComplete={this.handleColorChange}
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CLabel col md="3" htmlFor="file-input">
                    File input
                  </CLabel>
                  <CCol xs="12" md="9">
                    <CInputFile
                      onChange={this.handleFileChange}
                      id="file-input"
                      name="file-input"
                    />
                  </CCol>
                </CFormGroup>
              </CForm>
            </CCardBody>
            <CCardFooter>
              <CButton
                type="submit"
                className="m-1"
                size="sm"
                color="primary"
                onClick={this.submitButton}
              >
                Submit
              </CButton>
              <CButton
                type="reset"
                size="sm"
                className="m-1"
                color="danger"
                onClick={this.resetButton}
              >
                Reset
              </CButton>
            </CCardFooter>
          </CCard>
        </CCol>
      </>
    );
  }
}

export default AddChapter;
